import { Component, Input, Output, EventEmitter, HostListener, ElementRef, ViewChild } from '@angular/core';
import { CommonModule } from '@angular/common';

@Component({
  selector: 'agu-dropdown',
  standalone: false,
  templateUrl: './dropdown.component.html',
  styleUrl: './dropdown.component.scss'
})
export class DropdownComponent {
  @Input() trigger: 'click' | 'hover' = 'click';
  @Input() placement: 'bottom' | 'top' | 'left' | 'right' = 'bottom';
  @Input() visible: boolean = false;
  @Output() visibleChange = new EventEmitter<boolean>();
  @Output() onVisibleChange = new EventEmitter<boolean>();

  @ViewChild('dropdownMenu', { static: false }) dropdownMenu!: ElementRef;

  @HostListener('document:click', ['$event'])
  onClickOutside(event: Event): void {
    if (this.trigger === 'click' && this.visible) {
      const target = event.target as HTMLElement;
      if (!this.dropdownMenu?.nativeElement.contains(target) && 
          !(event.target as HTMLElement).closest('.agu-dropdown__trigger')) {
        this.close();
      }
    }
  }

  toggle(): void {
    this.visible = !this.visible;
    this.visibleChange.emit(this.visible);
    this.onVisibleChange.emit(this.visible);
  }

  open(): void {
    if (!this.visible) {
      this.visible = true;
      this.visibleChange.emit(true);
      this.onVisibleChange.emit(true);
    }
  }

  close(): void {
    if (this.visible) {
      this.visible = false;
      this.visibleChange.emit(false);
      this.onVisibleChange.emit(false);
    }
  }

  onMouseEnter(): void {
    if (this.trigger === 'hover') {
      this.open();
    }
  }

  onMouseLeave(): void {
    if (this.trigger === 'hover') {
      this.close();
    }
  }
}

