import { Component, Input } from '@angular/core';

@Component({
  selector: 'agu-table',
  standalone: false,
  templateUrl: './table.component.html',
  styleUrl: './table.component.scss'
})
export class TableComponent {
  @Input() striped: boolean = false;
  @Input() bordered: boolean = false;
  @Input() hoverable: boolean = false;
  @Input() size: 'sm' | 'md' | 'lg' = 'md';

  get classes(): string {
    const classes: string[] = [];
    if (this.striped) classes.push('table-striped');
    if (this.bordered) classes.push('table-bordered');
    if (this.hoverable) classes.push('table-hoverable');
    classes.push(`table-${this.size}`);
    return classes.join(' ');
  }
}

@Component({
  selector: 'agu-table-header',
  standalone: false,
  template: '<thead [ngClass]="classes"><ng-content></ng-content></thead>',
  styles: [':host { display: table-header-group; }']
})
export class TableHeaderComponent {
  @Input() variant: 'default' | 'dark' | 'light' = 'default';
  
  get classes(): string {
    return `table-header-${this.variant}`;
  }
}

@Component({
  selector: 'agu-table-body',
  standalone: false,
  template: '<tbody><ng-content></ng-content></tbody>',
  styles: [':host { display: table-row-group; }']
})
export class TableBodyComponent {
}

@Component({
  selector: 'agu-table-row',
  standalone: false,
  template: '<tr [ngClass]="classes"><ng-content></ng-content></tr>',
  styles: [':host { display: table-row; }']
})
export class TableRowComponent {
  @Input() variant: 'default' | 'primary' | 'success' | 'warning' | 'danger' | 'info' = 'default';
  
  get classes(): string {
    return this.variant !== 'default' ? `table-row-${this.variant}` : '';
  }
}

@Component({
  selector: 'agu-table-cell',
  standalone: false,
  template: '<td [ngClass]="classes"><ng-content></ng-content></td>',
  styles: [':host { display: table-cell; }']
})
export class TableCellComponent {
  @Input() align: 'left' | 'center' | 'right' = 'left';
  
  get classes(): string {
    return `text-${this.align}`;
  }
}

@Component({
  selector: 'agu-table-header-cell',
  standalone: false,
  template: '<th [ngClass]="classes"><ng-content></ng-content></th>',
  styles: [':host { display: table-cell; }']
})
export class TableHeaderCellComponent {
  @Input() align: 'left' | 'center' | 'right' = 'left';
  
  get classes(): string {
    return `text-${this.align}`;
  }
}

