import { Component, Input, OnInit, OnDestroy } from '@angular/core';
import { CommonModule } from '@angular/common';

@Component({
  selector: 'agu-text-effect',
  standalone: false,
  templateUrl: './text-effect.component.html',
  styleUrl: './text-effect.component.scss'
})
export class TextEffectComponent implements OnInit, OnDestroy {
  @Input() text: string = '';
  @Input() effect: 'typing' | 'fade' | 'slide' | 'glow' | 'gradient' | 'wave' = 'typing';
  @Input() speed: number = 100; // milliseconds per character for typing
  @Input() delay: number = 0;
  @Input() loop: boolean = false;
  @Input() color: string = '';
  @Input() fontSize: string = '1rem';

  displayedText: string = '';
  private typingTimer: any;
  private currentIndex: number = 0;
  private isAnimating: boolean = false;

  ngOnInit(): void {
    if (this.delay > 0) {
      setTimeout(() => this.startEffect(), this.delay);
    } else {
      this.startEffect();
    }
  }

  ngOnDestroy(): void {
    this.stopEffect();
  }

  private startEffect(): void {
    switch (this.effect) {
      case 'typing':
        this.startTyping();
        break;
      case 'fade':
      case 'slide':
      case 'glow':
      case 'gradient':
      case 'wave':
        this.displayedText = this.text;
        break;
    }
  }

  private startTyping(): void {
    this.displayedText = '';
    this.currentIndex = 0;
    this.isAnimating = true;
    this.typeNextChar();
  }

  private typeNextChar(): void {
    if (this.currentIndex < this.text.length) {
      this.displayedText += this.text[this.currentIndex];
      this.currentIndex++;
      this.typingTimer = setTimeout(() => this.typeNextChar(), this.speed);
    } else {
      this.isAnimating = false;
      if (this.loop) {
        setTimeout(() => this.startTyping(), 1000);
      }
    }
  }

  private stopEffect(): void {
    if (this.typingTimer) {
      clearTimeout(this.typingTimer);
    }
  }

  getFormattedText(): string {
    if (this.effect === 'wave') {
      return this.text.split('').map((char, index) => {
        if (char === ' ') return ' ';
        return `<span class="agu-text-effect__wave-char" style="animation-delay: ${index * 0.1}s">${char}</span>`;
      }).join('');
    }
    return this.text;
  }
}

