import { Component, Input, Output, EventEmitter, ContentChildren, QueryList, AfterContentInit } from '@angular/core';
import { CommonModule } from '@angular/common';
import { AccordionPanelComponent } from './accordion-panel.component';

@Component({
  selector: 'agu-accordion',
  standalone: false,
  templateUrl: './accordion.component.html',
  styleUrl: './accordion.component.scss'
})
export class AccordionComponent implements AfterContentInit {
  @Input() accordion: boolean = false; // Only one panel open at a time
  @Input() bordered: boolean = true;
  @Input() ghost: boolean = false;

  @ContentChildren(AccordionPanelComponent) panels!: QueryList<AccordionPanelComponent>;

  ngAfterContentInit(): void {
    this.panels.forEach(panel => {
      panel.bordered = this.bordered;
      panel.ghost = this.ghost;
    });
  }

  onPanelChange(activeKey: string): void {
    if (this.accordion) {
      this.panels.forEach(panel => {
        if (panel.key !== activeKey) {
          panel.active = false;
        }
      });
    }
  }
}

