import { Component, Input } from '@angular/core';
import { CommonModule } from '@angular/common';

@Component({
  selector: 'agu-tab',
  standalone: false,
  template: '<ng-content></ng-content>',
  styles: [`
    :host {
      display: block;
    }
  `]
})
export class TabComponent {
  @Input() key: string = '';
  @Input() title: string = '';
  @Input() icon: string = '';
  @Input() active: boolean = false;
}

