import { Component, Input, Output, EventEmitter } from '@angular/core';
import { CommonModule } from '@angular/common';

@Component({
  selector: 'agu-pagination',
  standalone: false,
  templateUrl: './pagination.component.html',
  styleUrl: './pagination.component.scss'
})
export class PaginationComponent {
  @Input() current: number = 1;
  @Input() total: number = 0;
  @Input() pageSize: number = 10;
  @Input() showSizeChanger: boolean = false;
  @Input() showQuickJumper: boolean = false;
  @Input() showTotal: boolean = false;
  @Input() simple: boolean = false;
  @Output() currentChange = new EventEmitter<number>();
  @Output() pageSizeChange = new EventEmitter<number>();

  get totalPages(): number {
    return Math.ceil(this.total / this.pageSize);
  }

  get pages(): number[] {
    const pages: number[] = [];
    const total = this.totalPages;
    const current = this.current;

    if (total <= 7) {
      for (let i = 1; i <= total; i++) {
        pages.push(i);
      }
    } else {
      if (current <= 4) {
        for (let i = 1; i <= 5; i++) {
          pages.push(i);
        }
        pages.push(-1); // ellipsis
        pages.push(total);
      } else if (current >= total - 3) {
        pages.push(1);
        pages.push(-1); // ellipsis
        for (let i = total - 4; i <= total; i++) {
          pages.push(i);
        }
      } else {
        pages.push(1);
        pages.push(-1); // ellipsis
        for (let i = current - 1; i <= current + 1; i++) {
          pages.push(i);
        }
        pages.push(-1); // ellipsis
        pages.push(total);
      }
    }

    return pages;
  }

  goToPage(page: number): void {
    if (page >= 1 && page <= this.totalPages && page !== this.current) {
      this.current = page;
      this.currentChange.emit(page);
    }
  }

  prev(): void {
    if (this.current > 1) {
      this.goToPage(this.current - 1);
    }
  }

  next(): void {
    if (this.current < this.totalPages) {
      this.goToPage(this.current + 1);
    }
  }

  onPageSizeChange(size: number): void {
    this.pageSize = size;
    this.current = 1;
    this.pageSizeChange.emit(size);
    this.currentChange.emit(1);
  }

  get Math() {
    return Math;
  }
}

