import { Component, Input, Output, EventEmitter, OnInit, OnDestroy, ViewChild, ElementRef, AfterViewInit, QueryList, ViewChildren } from '@angular/core';
import { CommonModule } from '@angular/common';

@Component({
  selector: 'agu-carousel',
  standalone: false,
  templateUrl: './carousel.component.html',
  styleUrl: './carousel.component.scss'
})
export class CarouselComponent implements OnInit, AfterViewInit, OnDestroy {
  @Input() autoplay: boolean = false;
  @Input() autoplayInterval: number = 3000;
  @Input() showDots: boolean = true;
  @Input() showArrows: boolean = true;
  @Input() effect: 'slide' | 'fade' = 'slide';
  @Input() dotsPosition: 'bottom' | 'top' | 'left' | 'right' = 'bottom';
  @Output() slideChange = new EventEmitter<number>();

  @ViewChild('carouselContainer', { static: false }) carouselContainer!: ElementRef;
  @ViewChildren('slide') slides!: QueryList<ElementRef>;

  currentIndex: number = 0;
  private autoplayTimer: any;
  private isTransitioning: boolean = false;

  ngOnInit(): void {
    if (this.autoplay) {
      this.startAutoplay();
    }
  }

  ngAfterViewInit(): void {
    this.updateCarousel();
  }

  ngOnDestroy(): void {
    this.stopAutoplay();
  }

  goToSlide(index: number): void {
    if (this.isTransitioning || index === this.currentIndex) return;
    
    const slideCount = this.slides?.length || 0;
    if (index < 0 || index >= slideCount) return;

    this.isTransitioning = true;
    this.currentIndex = index;
    this.updateCarousel();
    this.slideChange.emit(index);
    
    setTimeout(() => {
      this.isTransitioning = false;
    }, 500);
  }

  next(): void {
    const slideCount = this.slides?.length || 0;
    const nextIndex = (this.currentIndex + 1) % slideCount;
    this.goToSlide(nextIndex);
  }

  prev(): void {
    const slideCount = this.slides?.length || 0;
    const prevIndex = (this.currentIndex - 1 + slideCount) % slideCount;
    this.goToSlide(prevIndex);
  }

  private updateCarousel(): void {
    if (!this.carouselContainer || !this.slides) return;
    
    if (this.effect === 'slide') {
      const translateX = -this.currentIndex * 100;
      this.carouselContainer.nativeElement.style.transform = `translateX(${translateX}%)`;
    }
  }

  private startAutoplay(): void {
    this.stopAutoplay();
    this.autoplayTimer = setInterval(() => {
      this.next();
    }, this.autoplayInterval);
  }

  private stopAutoplay(): void {
    if (this.autoplayTimer) {
      clearInterval(this.autoplayTimer);
      this.autoplayTimer = null;
    }
  }

  onMouseEnter(): void {
    if (this.autoplay) {
      this.stopAutoplay();
    }
  }

  onMouseLeave(): void {
    if (this.autoplay) {
      this.startAutoplay();
    }
  }

  getSlideCount(): number {
    return this.slides?.length || 0;
  }
}

