import { Component, Input, Output, EventEmitter, forwardRef } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';

export interface SelectOption {
  label: string;
  value: any;
  disabled?: boolean;
}

@Component({
  selector: 'agu-select',
  standalone: false,
  templateUrl: './select.component.html',
  styleUrl: './select.component.scss',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => SelectComponent),
      multi: true
    }
  ]
})
export class SelectComponent implements ControlValueAccessor {
  @Input() options: SelectOption[] = [];
  @Input() placeholder: string = 'Select an option';
  @Input() disabled: boolean = false;
  @Input() size: 'sm' | 'md' | 'lg' = 'md';
  @Input() multiple: boolean = false;
  @Output() change = new EventEmitter<any>();

  selectedValue: any = null;
  isOpen: boolean = false;
  private onChange = (value: any) => {};
  private onTouched = () => {};

  get displayText(): string {
    if (this.multiple && Array.isArray(this.selectedValue)) {
      return this.selectedValue.length > 0 
        ? `${this.selectedValue.length} selected`
        : this.placeholder;
    }
    const option = this.options.find(opt => opt.value === this.selectedValue);
    return option ? option.label : this.placeholder;
  }

  toggle(): void {
    if (!this.disabled) {
      this.isOpen = !this.isOpen;
    }
  }

  selectOption(option: SelectOption): void {
    if (option.disabled) return;

    if (this.multiple) {
      if (!Array.isArray(this.selectedValue)) {
        this.selectedValue = [];
      }
      const index = this.selectedValue.indexOf(option.value);
      if (index > -1) {
        this.selectedValue.splice(index, 1);
      } else {
        this.selectedValue.push(option.value);
      }
    } else {
      this.selectedValue = option.value;
      this.isOpen = false;
    }
    
    this.onChange(this.selectedValue);
    this.onTouched();
    this.change.emit(this.selectedValue);
  }

  isSelected(option: SelectOption): boolean {
    if (this.multiple && Array.isArray(this.selectedValue)) {
      return this.selectedValue.includes(option.value);
    }
    return this.selectedValue === option.value;
  }

  writeValue(value: any): void {
    this.selectedValue = value;
  }

  registerOnChange(fn: (value: any) => void): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: () => void): void {
    this.onTouched = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }
}

