import { Component, Input, Output, EventEmitter, forwardRef } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';

@Component({
  selector: 'agu-rate',
  standalone: false,
  templateUrl: './rate.component.html',
  styleUrl: './rate.component.scss',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => RateComponent),
      multi: true
    }
  ]
})
export class RateComponent implements ControlValueAccessor {
  @Input() count: number = 5;
  @Input() allowHalf: boolean = false;
  @Input() disabled: boolean = false;
  @Input() allowClear: boolean = true;
  @Input() size: 'sm' | 'md' | 'lg' = 'md';
  @Output() change = new EventEmitter<number>();

  value: number = 0;
  hoverValue: number = 0;
  private onChange = (value: number) => {};
  private onTouched = () => {};

  get stars(): number[] {
    return Array.from({ length: this.count }, (_, i) => i + 1);
  }

  onStarClick(star: number): void {
    if (this.disabled) return;
    
    if (this.allowClear && this.value === star) {
      this.value = 0;
    } else {
      this.value = star;
    }
    
    this.hoverValue = 0;
    this.onChange(this.value);
    this.onTouched();
    this.change.emit(this.value);
  }

  onStarHover(star: number): void {
    if (this.disabled) return;
    this.hoverValue = star;
  }

  onStarLeave(): void {
    this.hoverValue = 0;
  }

  isActive(star: number): boolean {
    const currentValue = this.hoverValue || this.value;
    return star <= currentValue;
  }

  writeValue(value: number): void {
    this.value = value || 0;
  }

  registerOnChange(fn: (value: number) => void): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: () => void): void {
    this.onTouched = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }
}

