import { Component, Input, Output, EventEmitter, ContentChildren, QueryList, AfterContentInit } from '@angular/core';
import { CommonModule } from '@angular/common';
import { TabComponent } from './tab.component';

@Component({
  selector: 'agu-tabs',
  standalone: false,
  templateUrl: './tabs.component.html',
  styleUrl: './tabs.component.scss'
})
export class TabsComponent implements AfterContentInit {
  @Input() activeKey: string = '';
  @Input() type: 'line' | 'card' | 'editable-card' = 'line';
  @Input() size: 'sm' | 'md' | 'lg' = 'md';
  @Output() activeKeyChange = new EventEmitter<string>();
  @Output() onTabClick = new EventEmitter<string>();

  @ContentChildren(TabComponent) tabs!: QueryList<TabComponent>;

  ngAfterContentInit(): void {
    if (this.tabs.length > 0 && !this.activeKey) {
      this.activeKey = this.tabs.first.key;
    }
    this.tabs.forEach(tab => {
      tab.active = tab.key === this.activeKey;
    });
  }

  selectTab(key: string): void {
    this.activeKey = key;
    this.activeKeyChange.emit(key);
    this.onTabClick.emit(key);
    
    this.tabs.forEach(tab => {
      tab.active = tab.key === key;
    });
  }
}

